import React, { useState, useEffect, useRef } from "react";
import sym1 from "../../assets/images/slot/bg/item1.png";
import sym2 from "../../assets/images/slot/bg/item2.png";
import sym3 from "../../assets/images/slot/bg/item3.png";
import sym4 from "../../assets/images/slot/bg/item4.png";
import sym5 from "../../assets/images/slot/bg/item5.png";
import sym6 from "../../assets/images/slot/bg/item6.png";
import BTC from "../../assets/images/slot/img/btc.png";
import ETH from "../../assets/images/slot/img/eth.png";
import LTC from "../../assets/images/slot/img/ltc.png";
import arrow from "../../assets/images/slot/img/down-arrow.png";
import logo from "../../assets/images/slot/logo.png";
import { Helmet } from "react-helmet";
import "./slot.css";

import { useDispatch, useSelector } from "react-redux";
import {
  gameSound,
  joinGame,
  resetSlot,
  updateWallet,
} from "../../reducers/slotReducers";
import Layout from "../layout/layout";
import ShowFixBetAmount from "./ShowFixBetAmount";
import { toast } from "react-toastify";

const Slot = () => {
  const gameObj = useSelector((state) => state.game);
  const {
    userData: { cryptoWallet },
  } = gameObj ?? {};
  const dispatch = useDispatch();
  const { slot } = gameObj;
  const { pair, numberArray } = slot;
  console.log("pair,slot", gameObj);
  console.log("cryptoWallet", cryptoWallet);

  const [betAmount, setBetAmount] = useState(0);
  const [blockchain, setBlockChain] = useState("btc");
  const [startAnimation, setStartAnimation] = useState(false);
  const [reset, setReset] = useState(true);
  const IMAGES = [sym1, sym2, sym3, sym4, sym5, sym6];
  const [preloadImages1, setPreloadImages1] = useState([]);
  const [preloadImages2, setPreloadImages2] = useState([]);
  const [preloadImages3, setPreloadImages3] = useState([]);
  const [preloadImages4, setPreloadImages4] = useState([]);
  const [preloadImages5, setPreloadImages5] = useState([]);
  const [Currency, setCurrency] = useState(BTC);
  const [showSelect, setShowSelect] = useState(false);
  const [WalletAmount, setWalletAmount] = useState(0);
  const [showLine, setShowLine] = useState(false);

  useEffect(() => {
    if (cryptoWallet) {
      setWalletAmount(cryptoWallet[blockchain.toLowerCase()].amount);
    }
  }, [cryptoWallet, blockchain]);
  useEffect(() => {
    if (numberArray.length) {
      console.log("numberArray", numberArray);
      setStartAnimation(true);
      setReset(false);
    }
  }, [numberArray]);

  const handleChange = (e) => {
    const { value } = e.target;
    setBetAmount(value);
  };

  const shuffleArray = (array) => {
    const sArray = [...array];
    for (let i = sArray.length - 1; i > 0; i--) {
      let j = parseInt(Math.random() * i);
      let tmp = sArray[i];
      sArray[i] = sArray[j];
      sArray[j] = tmp;
    }
    return sArray;
  };

  const reShufflePreload = () => {
    let newPreloadArray = [];
    for (let i = 0; i < 20; i++) {
      const shuffleArray1 = shuffleArray(IMAGES);
      newPreloadArray = [...shuffleArray1, ...newPreloadArray];
    }
    setPreloadImages1(() => newPreloadArray);
    newPreloadArray = [];
    for (let i = 0; i < 20; i++) {
      const shuffleArray1 = shuffleArray(IMAGES); //
      newPreloadArray = [...shuffleArray1, ...newPreloadArray];
    }
    setPreloadImages2(() => newPreloadArray);

    newPreloadArray = [];
    for (let i = 0; i < 20; i++) {
      const shuffleArray1 = shuffleArray(IMAGES);
      newPreloadArray = [...shuffleArray1, ...newPreloadArray];
    }
    setPreloadImages3(() => newPreloadArray);

    newPreloadArray = [];
    for (let i = 0; i < 20; i++) {
      const shuffleArray1 = shuffleArray(IMAGES);
      newPreloadArray = [...shuffleArray1, ...newPreloadArray];
    }
    setPreloadImages4(() => newPreloadArray);

    newPreloadArray = [];
    for (let i = 0; i < 20; i++) {
      const shuffleArray1 = shuffleArray(IMAGES);
      newPreloadArray = [...shuffleArray1, ...newPreloadArray];
    }
    setPreloadImages5(() => newPreloadArray);
  };

  useEffect(() => {
    reShufflePreload();
  }, []);

  const handleSpin = () => {
    if (!betAmount || parseFloat(betAmount) <= 0) {
      toast.error("Please enter bet amount min 5", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        toastId: 1,
      });
      return;
    }
    const payload = {
      blockchain,
      amount: betAmount,
    };
    setShowLine(() => false);
    console.log(resetSlot());
    //dispatch(resetSlot());
    dispatch(joinGame(payload));
    setReset(true);
    dispatch(updateWallet(payload));
  };

  const getImage = (number) => {
    switch (number) {
      case 0:
        return sym1;
      case 1:
        return sym2;
      case 2:
        return sym3;
      case 3:
        return sym4;
      case 4:
        return sym5;
      case 5:
        return sym6;
    }
  };

  useEffect(() => {
    let myy = JSON.parse(localStorage.getItem("playSound"));
    if (myy === false) {
      dispatch(gameSound(false));
    } else if (myy === true) {
      dispatch(gameSound(true));
    } else {
      dispatch(gameSound(true));
    }
  }, []);

  const handleCurrency = (curr, amount) => {
    if (curr === "BTC") {
      setCurrency(BTC);
      setWalletAmount(amount);
      setShowSelect(false);
      setBlockChain("btc");
    } else if (curr === "ETH") {
      setCurrency(ETH);
      setWalletAmount(amount);
      setBlockChain("eth");
      setShowSelect(false);
    } else {
      setCurrency(LTC);
      setWalletAmount(amount);
      setBlockChain("ltc");
      setShowSelect(false);
    }
  };

  const useOutsideAlerter = (ref) => {
    useEffect(() => {
      const handleClickOutside = (event) => {
        if (ref.current && !ref.current.contains(event.target)) {
          if (event.srcElement.className !== "close-option") {
            setShowSelect(false);
          }
        }
      };
      document.addEventListener("mousedown", handleClickOutside);
      return () => {
        document.removeEventListener("mousedown", handleClickOutside);
      };
    }, [ref]);
  };
  const wrapperRef = useRef(null);
  useOutsideAlerter(wrapperRef);
  const handleSelect = () => {
    setShowSelect(true);
  };

  return (
    <Layout>

        <Helmet>
          <html className="game-view" />
        </Helmet>
      <div id="viewport">
        <div className="slot-container">
          <div id="container">

            <div className="slot-header">
            <div className="slot-header-content">
             {localStorage.getItem("usertoken") && (
                  <div className="close-option">
                    <div className="custom-select-option" ref={wrapperRef}>
                      <h6 onClick={handleSelect}>
                        <div className={`${showSelect ? `active-true` : `bet-amt`}`}>
                          <img src={Currency} className="icon-img" />{" "}
                          <ShowFixBetAmount betTotal={WalletAmount} />
                        </div>
                        <img src={arrow} alt="arrow" />
                      </h6>
                      {showSelect && (
                        <div className="select-option">
                          <p
                            onClick={() =>
                              handleCurrency("BTC", cryptoWallet["btc"]?.amount)
                            }
                          >
                            {" "}
                            <img src={BTC} alt="btc" />
                            <ShowFixBetAmount
                              betTotal={cryptoWallet["btc"]?.amount}
                            />
                          </p>
                          <p
                            onClick={() =>
                              handleCurrency("ETH", cryptoWallet["eth"]?.amount)
                            }
                          >
                            <img src={ETH} alt="eth" />
                            <ShowFixBetAmount
                              betTotal={cryptoWallet["eth"]?.amount}
                            />
                          </p>
                          <p
                            onClick={() =>
                              handleCurrency("LTC", cryptoWallet["ltc"]?.amount)
                            }
                          >
                            <img src={LTC} alt="ltc" />
                            <ShowFixBetAmount
                              betTotal={cryptoWallet["ltc"]?.amount}
                            />
                          </p>
                        </div>
                      )}
                    </div>
                  </div>
                )}

                  <div className="buy-btn-box">
                    <div className="buy-btn"></div>
                  </div>


                  <div className="menu-btn-box">
                    <div className="info-btn"></div>
                    <div className="menu-btn"></div>
                  </div>
                  
                </div>
            </div>

            <div id="overlay">
              {showLine && pair && pair.length
                ? pair.find((el) => el.pairType === "horizental-1") && (
                    <div id="winLine1" className="winLine"></div>
                  )
                : null}

              {showLine && pair && pair.length
                ? pair.find((el) => el.pairType === "horizental-2") && (
                    <div id="winLine2" className="winLine"></div>
                  )
                : null}

              {showLine && pair && pair.length
                ? pair.find((el) => el.pairType === "horizental-3") && (
                    <div id="winLine3" className="winLine"></div>
                  )
                : null}

              {showLine && pair && pair.length
                ? pair.find((el) => el.pairType === "cross-1") && (
                    <div id="winLine4" className="winLine"></div>
                  )
                : null}

              {showLine && pair && pair.length
                ? pair.find((el) => el.pairType === "cross-2") && (
                    <div id="winLine5" className="winLine"></div>
                  )
                : null}
            </div>

            <div className="reel-content">
            <div id="reels">
              {preloadImages1 &&
              preloadImages1.length > 0 &&
              preloadImages2 &&
              preloadImages2.length > 0 &&
              preloadImages3 &&
              preloadImages3.length > 0 &&
              preloadImages4 &&
              preloadImages4.length > 0 && 
              preloadImages5 &&
              preloadImages5.length > 0 ? (
                <>
                  <div className="reel">
                    {preloadImages1.map((el, i) => {
                      return (
                        <div className="slot-items">
                          <img
                            className={`${
                              !reset ? "reel-transition" : "reel-animation-end"
                            }`}
                            key={`HERE-${el + i}`}
                            src={
                              startAnimation &&
                              numberArray &&
                              numberArray.length &&
                              i >= 24 &&
                              i <= 26
                                ? getImage(numberArray[i - 24][0])
                                : el
                            }
                            alt={i}
                          />
                        </div>
                      );
                    })}
                  </div>
                  <div className="reel">
                    {preloadImages2.map((el, i) => {
                      return (
                        <div className="slot-items">
                          <img
                            className={`${
                              !reset ? "reel-transition" : "reel-animation-end"
                            }`}
                            key={`HERE-${el + i}`}
                            src={
                              startAnimation &&
                              numberArray &&
                              numberArray.length &&
                              i >= 24 &&
                              i <= 26
                                ? getImage(numberArray[i - 24][1])
                                : el
                            }
                            alt={i}
                          />
                        </div>
                      );
                    })}
                  </div>
                  <div className="reel">
                    {preloadImages3.map((el, i) => {
                      return (
                        <div className="slot-items">
                          <img
                            onAnimationEnd={() => {
                              if (i === 0) {
                                setShowLine(() => true);
                                // setReset(() => false);
                              }
                            }}
                            className={`${
                              !reset ? "reel-transition" : "reel-animation-end"
                            }`}
                            key={`HERE-${el + i}`}
                            src={
                              startAnimation &&
                              numberArray &&
                              numberArray.length &&
                              i >= 24 &&
                              i <= 26
                                ? getImage(numberArray[i - 24][2])
                                : el
                            }
                            alt={i}
                          />
                        </div>
                      );
                    })}
                  </div>
                  <div className="reel">
                    {preloadImages4.map((el, i) => {
                      return (
                        <div className="slot-items">
                          <img
                            onAnimationEnd={() => {
                              if (i === 0) {
                                setShowLine(() => true);
                                // setReset(() => false);
                              }
                            }}
                            className={`${
                              !reset ? "reel-transition" : "reel-animation-end"
                            }`}
                            key={`HERE-${el + i}`}
                            src={
                              startAnimation &&
                              numberArray &&
                              numberArray.length &&
                              i >= 24 &&
                              i <= 26
                                ? getImage(numberArray[i - 24][2])
                                : el
                            }
                            alt={i}
                          />
                        </div>
                      );
                    })}
                  </div>
                  <div className="reel">
                    {preloadImages5.map((el, i) => {
                      return (
                        <div className="slot-items">
                          <img
                            onAnimationEnd={() => {
                              if (i === 0) {
                                setShowLine(() => true);
                                // setReset(() => false);
                              }
                            }}
                            className={`${
                              !reset ? "reel-transition" : "reel-animation-end"
                            }`}
                            key={`HERE-${el + i}`}
                            src={
                              startAnimation &&
                              numberArray &&
                              numberArray.length &&
                              i >= 24 &&
                              i <= 26
                                ? getImage(numberArray[i - 24][2])
                                : el
                            }
                            alt={i}
                          />
                        </div>
                      );
                    })}
                  </div>
                </>
              ) : (
                <div className="spinner-border" role="status">
                  <span className="sr-only">Loading...</span>
                </div>
              )}
            </div>

            </div>

         

            <div className="slot-footer">

              <div id="balanceDiv">
                

                <div className="win-amount-box">
                  <div className="win-amount">
                    <input
                      type="number"
                      name="betAmount"
                      placeholder="0"
                    />
                </div>
                </div>


              <div className="bet-amount">
                <div className="minus-btn"></div>
                <input
                  type="number"
                  name="betAmount"
                  value={betAmount}
                  onChange={(e) => handleChange(e)}
                />
                <div className="plus-btn"></div>
              </div>

                <div className="spin-box">
                  <div
                      onClick={() => {
                        handleSpin();
                      }}
                      id="btnSpin"
                      className="btnSpin"></div>

                   <div className="max-bet-btn"></div>
                  </div>
                  
              </div>
          </div>


          </div>

         
        </div>

        <div className="logo-watermark">
          <img src={logo} alt="" />
        </div>
      </div>
    </Layout>
  );
};

export default Slot;
