import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { slotInstance } from "../config/axios";
import { toast } from "react-toastify";

const initialState = {
  userData: {},
  currentGameUsers: [],
  slot: {
    pair: [],
    numberArray: [],
  },
};

// get user Profile
export const getUserProfile = createAsyncThunk(
  "game/getUserProfile",
  async ({ rejectWithValue }) => {
    try {
      console.log("calledd");
      const res = await slotInstance().get("/getUserProfile");
      return {
        userData: res?.data,
      };
    } catch (err) {
      // localStorage.removeItem("usertoken");
      return rejectWithValue({
        msg: err.response.data.msg ?? err.response.data.error,
      });
    }
  }
);

// join game user
export const joinGame = createAsyncThunk(
  "slot/joinGame",
  async (data, { rejectWithValue }) => {
    try {
      console.log("data", data);
      const res = await slotInstance().post("/joinGame", data);
      console.log("res", res);
      return {
        slotData: res?.data,
        winAmount: res?.data.winAmount,
        blockchain: data.blockchain,
      };
    } catch (err) {
      console.log("err", err.response.data.msg);
      return rejectWithValue({
        msg: err.response.data.msg ?? "some error occured ",
        code: err.response.data.code,
        amount: data.amount,
        blockchain: data.blockchain,
      });
    }
  }
);

export const slotSlice = createSlice({
  name: "slot",
  initialState,
  reducers: {
    resetSlot: (state) => {
      state.slot = {
        numberArray: [],
        pair: [],
      };
    },
    gameSound: (state, action) => {
      state.playSound = action.payload;
    },
    updateWallet: (state, { payload }) => {
      state.userData.cryptoWallet[payload?.blockchain].amount =
        state.userData.cryptoWallet[payload?.blockchain].amount -
        payload?.amount;
    },
  },
  extraReducers: (builder) => {
    // getUserProfile   slice

    builder.addCase(getUserProfile.fulfilled, (state, { payload }) => {
      const userData = payload && payload.userData.findUser;
      const data = payload;
      return /* state = */ {
        ...state,
        userData,
        currentGameUsers: data.currentGameUsers,
      };
    });

    builder.addCase(joinGame.fulfilled, (state, { payload }) => {
      const { slotData } = payload && payload;
      console.log("payload", payload);
      //console.log("slotData====>>>> ", slotData);
      state.userData.cryptoWallet[payload?.blockchain].amount =
        state.userData.cryptoWallet[payload?.blockchain].amount +
        payload?.winAmount;
      state.slot = slotData;
      // return {
      // ...state,
      // slot: slotData,
      // };
    });

    builder.addCase(joinGame.rejected, (state, { payload }) => {
      const { msg } = payload || {};
      console.log("payload", payload);
      state.userData.cryptoWallet[payload?.blockchain].amount =
        state.userData.cryptoWallet[payload?.blockchain].amount +
        payload?.amount;
      toast.error(msg, {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        toastId: 1,
      });
    });
  },
});

export const { resetSlot, gameSound, updateWallet } = slotSlice.actions;

export default slotSlice.reducer;
