/* eslint-disable react/react-in-jsx-scope */
import propTypes from "prop-types";

const ShowFixBetAmount = ({ betTotal }) => {
  const toFixed = (x) => {
    if (Math.abs(x) < 1.0) {
      var am = parseInt(x.toString().split("e-")[1]);
      if (am) {
        x *= Math.pow(10, e - 1);
        x = "0." + new Array(e).join("0") + x.toString().substring(2);
      }
      return am;
    } else {
      var e = parseInt(x.toString().split("+")[1]);
      if (e > 20) {
        e -= 20;
        x /= Math.pow(10, e);
        x += new Array(e + 1).join("0");
      }
    }
    const str = "0." + x.split(".")[1].split("0").join("");
    return str;
  };

  let showAmount = 0;

  if (betTotal < 0) {
    showAmount = toFixed(betTotal);
  } else if (betTotal) {
    showAmount = betTotal.toFixed(2);
  }

  return <>{showAmount}</>;
};

export default ShowFixBetAmount;

ShowFixBetAmount.propTypes = {
  betTotal: propTypes.number,
};
