import React, { useState, useEffect } from "react";
import { Dropdown } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import user from "../../assets/images/header/owner.png";
import mainlogo from "../../assets/images/header/logo-main.png";
import mainlogoborder from "../../assets/images/header/logo-main-border.png";
import BTC from "../../assets/images/slot/img/btc.png";
import ETH from "../../assets/images/slot/img/eth.png";
import LTC from "../../assets/images/slot/img/ltc.png";
import menu1 from "../../assets/images/header/menu1.svg";
import menu2 from "../../assets/images/header/menu5.svg";
import { Link } from "react-router-dom";
import { gameSound, getUserProfile } from "../../reducers/slotReducers";
import { casinoServer } from "../../config/keys";
import "./header.css";


const Header = () => {
  const dispatch = useDispatch();
  const gameObj = useSelector((state) => state.game);
  const {
    userData: { username, profilePic, cryptoWallet },
  } = gameObj ?? {};
  const playSound = gameObj.playSound;

  console.log("userData", username, profilePic);
  const [isSticky, setIsSticky] = useState(false);
  const watchScroll = () => {
    if (window.scrollY > 60) {
      setIsSticky(true);
    } else {
      setIsSticky(false);
    }
  };
  useEffect(() => {
    dispatch(getUserProfile("profile"));
  }, []);
  useEffect(() => {
    if (window !== undefined && window.screen.width >= 0) {
      window.addEventListener("scroll", watchScroll);
    }
    return () => {
      window.removeEventListener("scroll", watchScroll);
    };
  }, []);

  const handlePlay = () => {
    localStorage.setItem("playSound", !playSound);
    dispatch(gameSound(!playSound));
  };
  return (
    <div className={`header ${isSticky ? " sticky" : ""}`}>
      <div className="container">
        <div className="header-top-area">
          <div className="main-game-list">
            <div className="header-logo wow animate__animated animate__slideInLeft">
              <Link to="/">
                <img src={mainlogo} alt="logo" className="main-logo" />
                <img
                  src={mainlogoborder}
                  alt="logo"
                  className="main-logo-border"
                />
              </Link>
            </div>
            <div className="game-list-content"></div>
            <div className="header-top-login">
              <div className="volume-btn">
                <button onClick={handlePlay}>
                  {playSound ? (
                    <i className="fa fa-volume-up"></i>
                  ) : (
                    <i className="fa fa-volume-down"></i>
                  )}
                </button>
              </div>
              {!localStorage.getItem("usertoken") ? (
                <>
                  <div className="loggedoff-menu wow animate__animated animate__slideInRight">
                    <ul>
                      <li>
                        <span
                          role="presentation"
                          //  onClick={() => handleModal("login")}
                        >
                          Login
                        </span>
                      </li>
                      <li>
                        <span
                          role="presentation"
                          //onClick={() => handleModal("register")}
                        >
                          Sign Up
                        </span>
                      </li>
                    </ul>
                  </div>

                  <div className="loggedin-menu wow animate__animated animate__slideInRight"></div>
                </>
              ) : (
                <>
                  <div className="wallet-input">
                    <Dropdown>
                      <Dropdown.Toggle variant="success" id="dropdown-profile">
                        <img src={BTC} alt="menu1" />
                        {cryptoWallet && cryptoWallet?.btc?.amount
                          ? cryptoWallet?.btc?.amount.toFixed(2)
                          : "0.00"}
                      </Dropdown.Toggle>
                      <Dropdown.Menu>
                        <Dropdown.Item>
                          <img src={LTC} alt="menu1" />
                          {cryptoWallet && cryptoWallet?.ltc?.amount
                            ? cryptoWallet?.ltc?.amount.toFixed(2)
                            : "0.00"}
                        </Dropdown.Item>
                        <Dropdown.Item>
                          <img src={ETH} alt="menu1" />{" "}
                          {cryptoWallet && cryptoWallet?.eth?.amount
                            ? cryptoWallet?.eth?.amount.toFixed(2)
                            : "0.00"}
                        </Dropdown.Item>
                      </Dropdown.Menu>
                    </Dropdown>
                  </div>
                  <div className="p-pic">
                    <img
                      src={profilePic ? `${casinoServer}/${profilePic}` : user}
                      alt="avtar"
                      onError={(e) => {
                        e.target.onerror = null;
                        e.target.src = user;
                      }}
                    />
                  </div>

                  <Dropdown>
                    <Dropdown.Toggle variant="success" id="dropdown-profile">
                      {username}
                    </Dropdown.Toggle>
                    <Dropdown.Menu>
                      <Dropdown.Item>
                        <img src={menu1} alt="gfgf" />
                        <Link to="/profile">Profile</Link>
                      </Dropdown.Item>
                      <Dropdown.Item>
                        <img src={menu1} alt="gfgf" />
                        <Link to="/withDrawDeposit">Withdraw/Deposit</Link>
                      </Dropdown.Item>
                      <Dropdown.Item>
                        <img src={menu2} alt="gshgdh" />
                        Log Out
                      </Dropdown.Item>
                    </Dropdown.Menu>
                  </Dropdown>
                </>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
